<template>
  <div id="app" class="main">
    <h1>{{ product.name }}</h1>
    <p class="description">{{ product.description }}</p>
    <div class="actions">
      <router-link v-bind:to="{ name: 'products-view' }"
        >Back to Products</router-link
      >&nbsp;|
      <router-link
        v-bind:to="{ name: 'new-review', params: { id: product.id } }"
