      >
    </div>
    <div class="well-display">
      <average-summary />
      <star-summary rating="1" />
      <star-summary rating="2" />
      <star-summary rating="3" />
      <star-summary rating="4" />
      <star-summary rating="5" />
    </div>
    <review-list />
  </div>
</template>



<script>
import AverageSummary from "../components/AverageSummary.vue";
import StarSummary from "../components/StarSummary.vue";
import ReviewList from "../components/ReviewList.vue";
export default {
  components: {
    AverageSummary,
    StarSummary,
    ReviewList,
  },
  computed: {
    product() {
      // return the active product object
      //   for (let i = 0; i < this.$store.state.products.length; i++){
      //       if (this.$store.state.products[i].id === this.$route.params.id)
      //       return this.$store.state.products[i];
      //   }

      //   return undefined;
      // return this.$store.state.products.find ((p) => p.id === this.$route.params.id);
      return this.$store.getters.product;
    },
  },
  created() {
    // life cycle hook
    const activeProductId = this.$route.params.id;
    this.$store.commit("SET_ACTIVE_PRODUCT", activeProductId);
  },
};
</script>

